body {
  font-family: $Font-primary;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

.container {
  max-width: 480px;
  margin: 0 auto;
  @include tablet {
    max-width: 768px;
    margin: 0 auto;
  }
  @include desktop {
    margin: 0 auto;
    max-width: 1280px;
  }
}
